<template>
    <div class="article">
        <ul>
            <li class="articlebox" v-for="(article,index) in articleShow" :key="index" @click="toArticles(article)">
                <div class="left">
                    <h1 class="tittle">{{article.title}}</h1>
                    <p class="content">{{article.description}}</p>
                    <p class="date">文章的发布时间</p>
                </div>
                <img class="right" src alt/>
            </li>
        </ul>
    </div>
</template>

<script>
    import {getArticleTitle} from "../../../../service/api";

    export default {
        name: "Article",
        data() {
            return {
                articleShow: []
            };
        },
        created() {
            //请求网络数据
            getArticleTitle()
                .then(response => {
                    this.articleShow = response.data;
                })
                .catch(error => {
                    console.log(error);
                });
        },
        methods: {
            toArticles(article) {
                this.$router.push({
                    path: `/articles/${article.id}`
                });
            }
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../../../style/_statement.scss";

    .article {
        margin: 5% auto;
        width: 90%;
        font-size: 16px;
    }

    .articlebox {
        height: 200px;
        margin: 20px;
        position: relative;
        cursor: pointer;
    }

    .left {
        display: inline-block;
        width: 70%;
    }

    .tittle {
        line-height: 30px;
        font-size: 30px;
        margin: 10px 0;
    }

    .content {
        font-size: 20px;
        line-height: 40px;
        margin: 10px 0;
    }

    .comment {
        font-size: 15px;
        line-height: 15px;
        margin: 40px 0;
    }

    .right {
        position: absolute;
        width: 25%;
        height: 80%;
        margin-left: 3%;
        box-shadow: 1px 1px 3px 3px rgba(0, 0, 0, 0.2);
    }

    @include styleOtherMPad {
        .article {
            margin: 0 auto;
        }
        .left {
            width: 100%;
        }
        .tittle {
            line-height: conversionPX(26);
            font-size: conversionPX(16);
            margin: conversionPX(5) auto;
        }

        .content {
            font-size: conversionPX(16);
            line-height: conversionPX(20);
            margin: 0;
        }

        .comment {
            font-size: conversionPX(12);
            line-height: conversionPX(13);
            margin: 10px 0;
        }
        .right {
            $wid: conversionPX(300);
            width: $wid;
            height: conversionPX(150);
            left: 50%;
            margin-left: -1.5rem;
            display: block;
        }
    }
</style>